import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)



// 把所有数组连接起来
// const allurl = indexs.concat(gits,vues,jss,canvas,mini,others);

export default new Router({
  mode: 'history',
  routes: [
    {path: '/',redirect:'index.html',component: resolve => require(['../components/index_datas.vue'],resolve)},
    {path: '/index.html',name: 'index',component: resolve => require(['../components/index_datas.vue'],resolve)},
    {path: '/detail/:id',name: 'detail',component: resolve => require(['../components/detail_datas.vue'],resolve)},
  ]
})





