<template>
    <div class="artical_detail typora-export os-windows">
        <!-- {{data_all}} -->
        <div v-html="data_all"></div>
    </div>
</template>
<script>
export default {
    name:'detail_datas',
    data(){
        return{
            data_all:''
        }
    },
    created(){
        console.log(this.$route.params.id);
        console.log(this.$router);
        var _this = this;
        this.$http.get(this.host+'/Indexgit/getDetail?id='+this.$route.params.id).then(res=>{
            console.log(res.data);
            _this.data_all = res.data.detail.content;
        },res=>{
            console.log(res);
            console.log('请求失败');
        })
    },
    mounted(){
        
    },
    updated(){
        window.scroll(0, 0);
    }
}


</script>

<style>
    @import'../assets/css/detail.css';
</style>





